  background-color: #eee;
  box-shadow: 0px 1rem 3rem 0.5rem rgba(0, 0, 0, 0.6);
  margin-bottom: 2rem;
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
  color: #000;
  -webkit-flex-direction: column;
          flex-direction: column;
